<template>
    <page class="cardSearch">
        <card>
            <h1>Seach cards</h1>
            <textBox label="Search" v-model="query" @change="performLazySearch()" @keypress.enter="performSearch()" />
            <button @click="performSearch()">Search</button>
            <div>
                <listItem v-for="card in results" :key="card.id">
                    <dropDown :label="card.name">
                        <cardDetails :card="card" :showCollectionControlls="true" :showPrints="true" />
                    </dropDown>
                </listItem>
            </div>
        </card>
    </page>
</template>

<script>
import cardDetails from "../components/cardDetails.vue";
import { page, card, textBox, listItem, dropDown } from "../components.js";

export default {
    components: {
        page,
        card,
        textBox,
        listItem,
        dropDown,
        cardDetails
    },
    data() {
        return {
            query: "",
            lastChange: 0,
            results: []
        }
    },
    methods: {
        async performSearch() {
            var response = await fetch("https://api.scryfall.com/cards/search?q=" + this.query).then(r => r.json());

            this.results = response.data;
        },
        performLazySearch() {
            var lazyDelay = 200;

            this.lastChange = Date.now();

            setTimeout(() => {
                if (this.query.length >= 5) {
                    var distance = Date.now() - this.lastChange;
                    console.log(distance);

                    if (distance >= lazyDelay) {
                        this.performSearch();
                    }
                }
            }, lazyDelay);
        }
    }
}
</script>

<style lang="scss" scoped>

.cardSearch {
    .card {
        max-width: 700px;
    }
}

</style>